.s3Swiper {
    top: 10%;
    .swiper {
        display: block;
        width: 100%;
        height: 100%;
        position: relative;
        
        
    }

    .swiper-slide {
        text-align: center;
        font-size: 18px;
        // background: #fff;

        /* Center slide text vertically */
        display: flex;
        justify-content: center;
        align-items: center;
    }

    .swiper-slide img {
        display: block;
        width: 100%;
        height: 100%;
        object-fit: cover;
    }
}
