.sub2-swiper {
  user-drag: none;
}
  .swiper-slide{
    padding-bottom: 100px;
  }

.sub2-tail {
  position: relative;

  .swiper-button-prev,
  .swiper-button-next {
    top: 90%;
    position: absolute;
    width: 30px;
    height: 30px;
    cursor: pointer;
    color: yellow;
    font-weight: bold;
    z-index: 10; /* 다른 요소 위에 표시되도록 z-index 설정 */
  }
  .swiper-button-prev {
    left: calc(50% - 50px); 
  
  }

  .swiper-button-next {
    right: calc(50% - 50px);
  }
}

