/* ************************************************* */
/* 모달 팝업창 스타일 */
.modal {
  position: fixed; /* 고정 위치로 설정하여 스크롤에 따라 움직이지 않도록 합니다. */
  z-index: 999; /* 다른 요소 위에 나타나도록 z-index를 설정합니다. */
  left: 0;
  top: 0;
  width: 100%; /* 화면 전체를 차지하도록 너비를 100%로 설정합니다. */
  height: 100%; /* 화면 전체를 차지하도록 높이를 100%로 설정합니다. */
  background-color: rgba(
    0,
    0,
    0,
    0.7
  ); /* 배경색을 반투명 검은색으로 설정합니다. */
  overflow: auto; /* 모달 내용이 넘칠 경우 스크롤이 나타나도록 합니다. */
  display: flex; /* Flexbox 레이아웃을 사용하여 내부 요소를 배치합니다. */
  justify-content: center; /* 가로 중앙 정렬을 위해 가로 방향으로 정렬합니다. */
  align-items: center; /* 세로 중앙 정렬을 위해 세로 방향으로 정렬합니다. */
  display: none;
}
/* 모달 내용 스타일 */
.modal-content {
  max-width: 90%; /* 최대 너비를 설정합니다. */
  max-height: 90%; /* 최대 높이를 설정합니다. */
}
/* 이미지 스타일ㅎ */
.modal-content {
  position: relative;
  display: block;
  width: 100%;
  height: auto;
  object-fit: contain;
  margin: 0 auto;
  top: 50%;
  transform: translateY(-50%);
}
